/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>After reading the article <a href=\"http://linux.oreillynet.com/pub/a/linux/2002/10/10/intro_gentoo.html\">Gentoo Linux Reloaded</a>, I decided to give <a href=\"http://www.gentoo.org\">Gentoo Linux</a> a try, despite the <a href=\"http://diveintomark.org/archives/2002/05/06.html\">bad experiences</a> of some that I have read about. The portage system, baed on FreeBSD ports, sounds good, and this could finally be a maintainable source-based Linux system. Even if you have an install CD, there's still a lot to download, since the sources are not on the CD (I cheated and did a test install at work, then burned a CD with the distfiles). But even though you no longer have to compile your own compiler when installing, you still have to compile your own kernel. And they don't let you copy the kernel from the install CD. I guess that would be cheating. I haven't compiled a Linux kernel from scratch in many moons, we'll see how many tries it takes to get something that boots. My test install at work didn't boot, but it was under VMWare so maybe it was doomed to fail anyway. <b>Update</b> In the time it took to write this post, the kernel finished compiling and worked on the first try. However, grub took two tries to work correctly. But now my Gentoo is happily updating itself.</p>","fields":{"title":"Gentoo Linux","date":"30 Oct 2002","url":"https://papascott.de/2002-10-30-gentoo-linux/"}}},"pathContext":{"slug":"/2002-10-30-gentoo-linux/","prev":{"fields":{"title":"Cold Shoulder","date":"30 Oct 2002","slug":"/2002-10-30-cold-shoulder/"}},"next":{"fields":{"title":"Sweet or Sour!","date":"31 Oct 2002","slug":"/2002-10-31-sweet-or-sour/"}}}}

/***/ })

